<template>
    <el-container>
        <el-main>
            <el-form :model="saveForm" ref="saveForm">
                <el-table
                    :data="saveForm.tData"
                    class="severitytable"
                    style="width: 100%"
                >
                    <el-table-column prop="" label="可能性类型" width="200">
                        <template slot-scope="scope">
                            <span style="vertical-align: middle"
                                >{{ scope.row.FTCODE }}.{{
                                    levelName[scope.row.FTCODE]
                                }}</span
                            >
                        </template>
                    </el-table-column>
                    <el-table-column prop="" label="值范围" width="270">
                        <template slot-scope="scope">
                            <el-input
                                style="width: 50px"
                                size="mini"
                                v-model="scope.row.ftfrom"
                            ></el-input
                            ><span>~</span>
                            <el-input
                                style="width: 50px"
                                size="mini"
                                v-model="scope.row.ftto"
                            ></el-input>
                        </template>
                    </el-table-column>
                </el-table>
            </el-form>
            <el-button
                type="primary"
                style="margin-top: 15px"
                @click.native="saveSubmit"
                :loading="saveLoading"
                >保存</el-button
            >
        </el-main>
    </el-container>
</template>
<script>
import { getfrequency, setfrequency } from "../../api/api";
import { getButtonList } from "../../promissionRouter";
export default {
    data() {
        return {
            canSave: false,
            saveForm: {
                tData: [],
            },
            levelName: {
                A: "几乎不发生",
                B: "很少发生",
                C: "偶尔发生",
                D: "可能发生",
                E: "经常发生",
            },
            saveLoading: false,
            options: [
                {
                    value: 1,
                    label: "低",
                },
                {
                    value: 2,
                    label: "中",
                },
                {
                    value: 3,
                    label: "高",
                },
                {
                    value: 4,
                    label: "超高",
                },
            ],
        };
    },
    methods: {
        getAllData() {
            getfrequency().then((res) => {
                if (res.success) {
                    this.saveForm.tData = res.data;
                }
            });
        },
        saveSubmit: function () {
            let _this = this;

            this.saveLoading = true;
            let para = Object.assign({}, this.saveForm);
            setfrequency(para).then((res) => {
                if (res.success) {
                    this.$message({
                        message: "设置成功",
                        type: "success",
                    });

                    this.getAllData();
                } else {
                    this.$message({
                        message: res.msg,
                        type: "error",
                    });
                }
                this.saveLoading = false;
            });
        },
    },
    mounted() {
        this.getAllData();
    },
};
</script>

<style scoped>
.severitytable /deep/ .el-input__inner {
    padding-left: 2px;
    padding-right: 2px;
}
</style>


